<template>
	<div class="orderDeliver">
		<header><i class="iconfont icon-xiangzuo1"  @click="goPrev"></i><span>交付</span></header>
		<div class="container">
			<div class="signatureBox">
			    <div class="visaDetailTop">电子签名</div>
			    <div class="canvasBox" ref="canvasHW">
		       		<canvas  id="thecanvas" @touchstart='touchStart'
		                @touchmove='touchMove'
		                @touchend='touchEnd'
		                ref="canvasF"
		                @mousedown="mouseDown"
		                @mousemove="mouseMove"
		                @mouseup="mouseUp"></canvas>
		        	<div class="btnBox"   @click="overwrite">重写</div>
		      	</div>
		    </div>
		    <div class="uploadImg">
				<div class="LoadImg_head">上传照片<span>(限6张)：</span></div>
				<div>
					<el-upload action="http://120.237.87.102:8081/tmsApp/uploadPicture.do"  :limit="6" :multiple="true"  :data="uploadParams" ref='upload_ref' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</div>
			</div>
			<div class="submitBtn">
				<p class="g_signBtn"  @click="submit">提交</p>
			</div>
			<div v-if="ifTips" class="msg_tips">
				<i class="iconfont icon-chenggong1"></i>
				<p>{{tips_Msg}}</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">

	import $ from "jquery"

	export default{
		name:"orderDeliver",
		data(){
			return{
				dialogImageUrl: '',
		        dialogVisible: false,//是否放大预览照片
		        uploadParams:{},//上传图片时 额外的参数
		        ifSign:false,//是否签名
		        tips_Msg:"",//提示信息
				ifTips:false,//提示信息是否显示
		        //
		        points:[],
		        canvasTxt:null,
		        startX:0,
		        startY:0,
		        moveY:0,
		        moveX:0,
		        endY:0,
		        endX:0,
		        w:null,
		        h:null,
		        isDown:false
			}
		},
      	mounted(){
	        let canvas=this.$refs.canvasF;
	        canvas.height = this.$refs.canvasHW.offsetHeight;
	        canvas.width =this.$refs.canvasHW.offsetWidth;
	        this.canvasTxt=canvas.getContext("2d");
		},
		created(){

			this.WaybillInfo = this.$store.state.WaybillInfo;

			this.uploadParams.tenantCode = this.WaybillInfo.tenantCode;//租户代码

			this.uploadParams.code = this.$route.query.deliverNo_list;//运单号(交付传运单号过来，到车和中途反馈传配载单号过来)

			this.uploadParams.codeType = "ORDER";//类型为 交付

			this.uploadParams.codeId = this.$route.query.deliverNo_list;//交付时=deliveryId,其他两个=shipmentId
			
		},
		methods:{
			goPrev(){
				var that = this;

				this.$router.push({
					name:"OrderDetails",
					query:{
					}
				})
			},
			submit(){

				var that = this;

				if(!this.ifSign){

					that.$alert('请签名', '提示', {
			            confirmButtonText: '确定'
		           	})

		           	return;
				}else if(!$(".uploadImg ul>li").length){

					that.$alert('请上传图片', '提示', {
			            confirmButtonText: '确定'
		           	})

		           	return;
				}

				// 上传图片
				this.$refs.upload_ref.submit();

				// 将电子签名图片转为base64编码
				var oCanvas = document.getElementById("thecanvas");
				var context = oCanvas.getContext("2d");

				// 将canvas的透明背景设置成白色
				var imageData = context.getImageData(0, 0, oCanvas.width, oCanvas.height);

				for(var i = 0; i < imageData.data.length; i += 4) {
				    // 当该像素是透明的，则设置成白色
				    if(imageData.data[i + 3] == 0) {
				        imageData.data[i] = 255;
				        imageData.data[i + 1] = 255;
				        imageData.data[i + 2] = 255;
				        imageData.data[i + 3] = 255; 
				    }
				}
				context.putImageData(imageData, 0, 0);

				// 将电图片转为base64编码
				var strDataURI = oCanvas.toDataURL();

				var deliverNo_list = this.$route.query.deliverNo_list;

				var postData = {
					tenantCode:  this.$store.state.WaybillInfo.tenantCode,//组织代码
					deliveryIds: deliverNo_list,//运单ID，比如：1001,1002,1003)，一个或多个交付统一传数组字符串
					driverName: that.$store.state.userInfo.userName,//司机名称，当前APP登录用户名
					autograph:strDataURI,// 电子签名
				}

				this.httpRequest( "deliverConfirmation.do",postData,function(res){

					that.ifTips = true;
					that.tips_Msg = "操作成功";

					setTimeout(function(){

						that.ifTips = false;
						that.toWaybill();
					},1000)
				})

			},
			// 预览放大图片
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			},
	        //电脑设备事件
	        mouseDown(ev){
		        ev = ev || event;
		        ev.preventDefault(); 
		        if(1){
		            let obj={
		              x:ev.offsetX,
		              y:ev.offsetY
		            };
		            this.startX=obj.x;
		            this.startY=obj.y;
		            this.canvasTxt.beginPath();
		            this.canvasTxt.moveTo(this.startX, this.startY);
		            this.canvasTxt.lineTo(obj.x, obj.y);
		            this.canvasTxt.stroke();
		            this.canvasTxt.closePath();
		            this.points.push(obj);
		            this.isDown=true;
		        }
	        },
	        //移动设备事件
	        touchStart(ev){
	          	ev = ev || event;
	          	ev.preventDefault();

	          	if(ev.touches.length == 1) {
		            let obj={
		              	x:ev.targetTouches[0].clientX,
		              	y:ev.targetTouches[0].clientY-48
		            };
		            this.startX=obj.x;
		            this.startY=obj.y;
		            this.canvasTxt.beginPath();
		            this.canvasTxt.moveTo(this.startX, this.startY);
		            this.canvasTxt.lineTo(obj.x, obj.y);
		            this.canvasTxt.stroke();
		            this.canvasTxt.closePath();
		            this.points.push(obj);
	          }
	        },
	        //电脑设备事件
	        mouseMove(ev){
	          	ev = ev || event;
	          	ev.preventDefault();

	          	this.ifSign = true;
	          	if(this.isDown){ 
		            let obj={
		              x:ev.offsetX,
		              y:ev.offsetY
		            };
		            this.moveY=obj.y;
		            this.moveX=obj.x;
		            this.canvasTxt.beginPath();
		            this.canvasTxt.moveTo(this.startX, this.startY);
		            this.canvasTxt.lineTo(obj.x, obj.y);
		            this.canvasTxt.stroke();
		            this.canvasTxt.closePath();
		            this.startY=obj.y;
		            this.startX=obj.x;
		            this.points.push(obj);
	          	}
	        },
	        //移动设备事件
	        touchMove(ev){
	          	ev = ev || event;
	          	ev.preventDefault();

	          	this.ifSign = true;
	          	if(ev.touches.length == 1) {

		            let obj={ 
		              x:ev.targetTouches[0].clientX,
		              y:ev.targetTouches[0].clientY-48
		            };
		            this.moveY=obj.y;
		            this.moveX=obj.x;
		            this.canvasTxt.beginPath();
		            this.canvasTxt.moveTo(this.startX, this.startY);
		            this.canvasTxt.lineTo(obj.x, obj.y);
		            this.canvasTxt.stroke();
		            this.canvasTxt.closePath();
		            this.startY=obj.y;
		            this.startX=obj.x;
		            this.points.push(obj);
		        }
	        },
	        //电脑设备事件
	        mouseUp(ev){
	          	ev = ev || event;
	          	ev.preventDefault();

	          	if(1){
		            let obj={
		              x:ev.offsetX,
		              y:ev.offsetY
		            };
		            this.canvasTxt.beginPath();
		            this.canvasTxt.moveTo(this.startX, this.startY);
		            this.canvasTxt.lineTo(obj.x, obj.y);
		            this.canvasTxt.stroke();
		            this.canvasTxt.closePath();
		            this.points.push(obj);
		            this.points.push({x:-1,y:-1});
		            this.isDown=false;
		        }
	        },
	        //移动设备事件
	        touchEnd(ev){
	          	ev = ev || event;
	          	ev.preventDefault();

	          	if(ev.touches.length == 1) {
		            let obj={
		              x:ev.targetTouches[0].clientX,
		              y:ev.targetTouches[0].clientY-48
		            };
		            this.canvasTxt.beginPath();
		            this.canvasTxt.moveTo(this.startX, this.startY);
		            this.canvasTxt.lineTo(obj.x, obj.y);
		            this.canvasTxt.stroke();
		            this.canvasTxt.closePath();
		            this.points.push(obj);
		            this.points.push({x:-1,y:-1});
	          	}
	        },
	        //重写
	        overwrite(){
		        this.canvasTxt.clearRect(0, 0, this.$refs.canvasF.width, this.$refs.canvasF.height);
		        this.points=[];
		        this.ifSign = false;
	        }
		}
	}
</script>
<style lang="less" scoped>
	.orderDeliver{
		.container{
			padding-bottom:150/50rem;
			.uploadImg{
				padding: 0 20/50rem;
				box-sizing: border-box;
				font-size: 30/50rem;
				overflow: hidden;
				&>.LoadImg_head{
					float: none;
					margin-bottom:10/50rem; 
					span{
						color: #aaa;
					}
				}
				&>div{
					float: left;
				}
			}
		}
	}

	.signatureBox{
	    width: 100%;
	    height: 550/50rem;
	    box-sizing: border-box;
	    overflow: hidden;
	    background: #fff;
	    z-index: 100;
	    display: flex;
	    flex-direction: column;
	}
	.visaDetailTop{
	    width: 100%;
	    height: 60/50rem;
	    line-height: 60/50rem;
	    text-align: center;
	    font-size: 30/50rem;
	}
	.canvasBox{
	  	width: 750/50rem;
	    padding: 0 5/50rem;
	    box-sizing: border-box;
	    flex: 1;
	}
	canvas{
	  	width: 100%;
	  	height: 400/50rem;
	    border: 1/50rem solid #71b900;
	    background-color: #fff;
	}
	.btnBox{
	  	width: 120/50rem;
	    height: 50/50rem;
	    line-height: 50/50rem;
	    background-color: #71b900;
	    color: #fff;
	    font-size: 28/50rem;
	    text-align: center;
	    border-radius: 8/50rem;
	    margin: 10/50rem auto;
	}
	@media only screen and (min-width: 750px){
	    .signatureBox{
	        position: absolute;
	        top: 0/50rem;
	        left: 0/50rem;
	        width: 100%;
	        min-height: 100%;
	        box-sizing: border-box;
	        overflow: visible;
	    }
	}
</style>


