import Vue from 'vue'
import Router from 'vue-router'
// 登录
import Login from '@/pages/Login/Login'  //登录
import Reg from '@/pages/Login/reg'  //注册
import ForgotPwd from '@/pages/Login/forgotPwd'  //忘记密码
import Identifying from '@/pages/Login/identifying'  //验证手机号


// 帐号与第三方关联
import Binding from "@/pages/Login/Binding" // 微信openid关联到帐号

// 搜索
import Search from '@/pages/search'

//我的任务
import Waybill from '@/pages/Waybill/Waybill'  //我的任务首页
import LoadingAppoint from '@/pages/Waybill/LoadingAppoint'  //装货预约 
import UnloadAppoint from '@/pages/Waybill/UnloadAppoint'  //卸货预约 
import WayFeedback from '@/pages/Waybill/WayFeedback'  //途中反馈
import DepartureConfirm from '@/pages/Waybill/DepartureConfirm'  //发车确认
import ArrivalConfirm from '@/pages/Waybill/ArrivalConfirm'  //到达确认
import WaybillDetail from '@/pages/Waybill/WaybillDetail/WaybillDetail'  //运单详情

//我的任务模块  交付
import OrderDetails from '@/pages/Waybill/Deliver/OrderDetails'  //按地址交付
import orderDeliver from '@/pages/Waybill/Deliver/orderDeliver'  //订单交付

// 我要找货
import FindGoods from '@/pages/findGoods/findGoods'  //我要找货首页

// 抢货竞价
import GrabGoods from '@/pages/grabGoods/grabGoods'  //抢货竞价首页

// 个人中心
import HomeIndex from '@/pages/home/home'  //个人中心首页



Vue.use(Router)

export default new Router({
  routes: [
    // 登录
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    // 帐号与第三方关联
    {
      path: '/Binding',
      name: 'Binding',
      component: Binding
    },
    {
      path: '/Reg',
      name: 'Reg',
      component: Reg
    },
    {
      path: '/ForgotPwd',
      name: 'ForgotPwd',
      component: ForgotPwd
    },
    {
      path: '/Identifying',
      name: 'Identifying',
      component: Identifying
    },
    // 搜索
    {
      path: '/Search',
      name: 'Search',
      component: Search
    },
    // 我的任务
    {
      path: '/Waybill',
      name: 'Waybill',
      component: Waybill
    },
    {
      path: '/LoadingAppoint',
      name: 'LoadingAppoint',
      component: LoadingAppoint
    },
    {
      path: '/UnloadAppoint',
      name: 'UnloadAppoint',
      component: UnloadAppoint
    },
    {
      path: '/WayFeedback',
      name: 'WayFeedback',
      component: WayFeedback
    },
    {
      path: '/DepartureConfirm',
      name: 'DepartureConfirm',
      component: DepartureConfirm
    },
    {
      path: '/ArrivalConfirm',
      name: 'ArrivalConfirm',
      component: ArrivalConfirm
    },
    {
      path: '/WaybillDetail',
      name: 'WaybillDetail',
      component: WaybillDetail
    },
    //我的任务模块  交付
    {
      path: '/OrderDetails',
      name: 'OrderDetails',
      component: OrderDetails
    },
    {
      path: '/orderDeliver',
      name: 'orderDeliver',
      component: orderDeliver
    },
    // 我要找货
    {
      path: '/FindGoods',
      name: 'FindGoods',
      component: FindGoods
    },
    // 抢货竞价
    {
      path: '/GrabGoods',
      name: 'GrabGoods',
      component: GrabGoods
    },
    // 个人中心
    {
      path: '/HomeIndex',
      name: 'HomeIndex',
      component: HomeIndex
    },
  ]
})



