<template>
	<div class="Waybill"  >
		<header><span>我的任务</span><i class="iconfont icon-huabanfuben"  @click="search"  ></i></header>
		<div class="container" ref="container">
				<div   class="bigList" v-for='(dataItem,index) in bigListData'  :id="index"  :key='index' >
					<!-- 搜索后结果 -->
					<div v-if="!NoSearchFrom" class="searchResult">
						<!-- 搜索后 单号与日期 -->
						<div  class="place"><span class="dateLoading">2018-08-11</span><span class="OrderNum">单号：{{dataItem.shipmentCode}}</span></div>
						<!-- 起终点城市 -->
						<div  class="firstLine">
							<span>{{dataItem.startCity}}</span>
							<i class="iconfont icon-jiang" ></i>
							<span>{{dataItem.endCity}}</span>
						</div>
						<div>
							<span>货&nbsp;&nbsp;&nbsp;量：</span>
							<span>{{dataItem.orderNum}} 单 &nbsp; </span>
							<span>{{(dataItem.vol * 0.000001).toFixed(3)}} 立方米 &nbsp; </span>
							<span>{{(dataItem.wt * 0.000001).toFixed(3)}} 吨 &nbsp; </span>
							<span>{{dataItem.qty}}箱</span>
						</div>
						<div v-if="dataItem.issueAddrLiat.length==1">
							<span>联系人：</span>
							<span>{{dataItem.issueAddrLiat[0].issueUser}}&nbsp;&nbsp;&nbsp; {{dataItem.issueAddrLiat[0].issuePhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i @click="callPhone(dataItem.issueAddrLiat[0].issuePhone)" class="iconfont icon-dianhua-copy" ></i>
						</div>
						<div v-if="dataItem.issueAddrLiat.length==1">
							<span>装货地址：</span>
							<span>{{dataItem.issueAddrLiat[0].issueAddr1}}</span>
						</div>
						<div v-else @click="MultiAddress(index)">
							<i class="iconfont icon-tishi" ></i>
							<span style="color:#5965D8; ">查看多装货地址</span>
						</div>
						<div class="detailBtn">
					    	<span  class="g_btn" @click="toOrderDeliver(dataItem.deliveryList)">详情</span>
					    </div>
					</div>
					<!-- 正常数据 -->
					<div v-if="NoSearchFrom" class="listHead">
						<!-- 导航 -->
						<span  @click="Navigation(index)" class="Navigation">导航</span>
						<!-- 状态 -->
						<span  class="LicensePlate">{{dataItem.status}}</span>
						<!-- 起终点城市 -->
						<div  class="firstLine">
							<span>{{dataItem.startCity}}</span>
							<i class="iconfont icon-jiang" ></i>
							<span>{{dataItem.endCity}}</span>
						</div>
						<!-- 单号 -->
						<div>
							<i  class="iconfont icon-danhao" ></i>
							<span>单&nbsp;&nbsp;&nbsp;号：</span>
							<span>{{dataItem.shipmentCode}}</span>
						</div>
						<div>
							<i class="iconfont icon-huowuzhongliang" ></i>
							<span>货&nbsp;&nbsp;&nbsp;量：</span>
							<span>{{dataItem.orderNum}} 单 &nbsp; </span>
							<span>{{(dataItem.vol * 0.000001).toFixed(3)}} 立方米 &nbsp; </span>
							<span>{{(dataItem.wt * 0.000001).toFixed(3)}} 吨 &nbsp; </span>
							<span>{{dataItem.qty}}箱</span>
						</div>
						<div v-if="dataItem.issueAddrLiat.length==1">
							<i class="iconfont icon-xingming" ></i>
							<span>联系人：</span>
							<span>{{dataItem.issueAddrLiat[0].issueUser}}&nbsp;&nbsp;&nbsp; {{dataItem.issueAddrLiat[0].issuePhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i @click="callPhone(dataItem.issueAddrLiat[0].issuePhone)" class="iconfont icon-dianhua-copy" ></i>
						</div>
						<div v-if="dataItem.issueAddrLiat.length==1">
							<i class="iconfont icon-qingshuruxiangxidizhi" ></i>
							<span>装货地址：</span>
							<span>{{dataItem.issueAddrLiat[0].issueAddr1}}</span>
						</div>
						<div v-else @click="MultiAddress(index)">
							<i class="iconfont icon-tishi" ></i>
							<span style="color:#5965D8; ">查看多装货地址</span>
						</div>
						<p>
							<span v-if="dataItem.status == '新建' || dataItem.status == '取消在途' "  @click="LoadingAppoint(index)">装货预约</span>
							<span v-else style="background-color: #aaa;">装货预约</span>
							<span v-if="dataItem.status == '新建' || dataItem.status == '取消在途'"  @click="toArrivalConfirm(index)">到达确认</span>
							<span v-else   style="background-color: #aaa;">到达确认</span>
							<span v-if="dataItem.status == '新建' || dataItem.status == '取消在途'"  @click="toDepartureConfirm(index)">发车确认</span>
							<span v-else   style="background-color: #aaa;">发车确认</span>
							<span v-if="dataItem.status == '在途' "  @click="toWayFeedback(index)">途中反馈</span>
							<span v-else   style="background-color: #aaa;">途中反馈</span>
						</p>
					</div>
					<div class="smallLiat" v-if="NoSearchFrom" v-for='(itemData,idx) in dataItem.deliveryList'  :id="idx"  :key='idx' >
						<span class='status'>{{itemData.status}}</span>
						<div>
							<span>客户名称：</span>
							<span>{{itemData.name}}</span>
						</div>
						<div>
							<span>送货地址：</span>
							<span>{{itemData.recAddr1}}</span>
						</div>
						<div>
							<span>联&nbsp; 系&nbsp;人：</span>
							<span>{{itemData.recName}}&nbsp;&nbsp;&nbsp; {{itemData.recPhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i @click="callPhone(itemData.recPhone)" class="iconfont icon-dianhua-copy" ></i>
						</div>
						<div>
							<span>运&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;量：</span>
							<span>{{(itemData.shippedVol * 0.000001).toFixed(3)}} 立方米 &nbsp;&nbsp; </span>
							<span>{{(itemData.shippedWt * 0.000001).toFixed(3)}} 吨 &nbsp;&nbsp; </span>
							<span>{{itemData.shippedQty}} 箱</span>
						</div>
						<div>
							<span>派送要求：</span>
							<span>{{itemData.send}}</span>
						</div>
						<p>
							<!-- 预约时间 -->
							<i  style="float: left;" :style="{ color: timeCompare(getNowTime(),itemData.appointDeliveryTime)}"    class="iconfont"  :class="{'icon-shijian':itemData.appointDeliveryTime}" ></i>
							<span  :style="{ color: timeCompare(getNowTime(),itemData.appointDeliveryTime)}">{{itemData.appointDeliveryTime}}</span>

							
							<span  @click="toOrderDetails(dataItem.shipmentID,dataItem.tenantCode,dataItem.deliveryList[idx])">交货确认</span>
							<span   v-if="dataItem.status == '在途' "   @click="toUnloadAppoint(dataItem.vehicleCode,dataItem.shipmentID,itemData.orgnizationId,itemData.recAddr1,dataItem.tenantCode)">卸货预约</span>
						</p>
					</div>
				</div>
				
				<!-- 查看多装货地址弹出框 -->
				<div class="MultiAddress">
					<div>
						<i @click="closeAddress"  class="iconfont icon-iconfontcuowu" ></i>
						<div class="addressHead">
							<span  @click="address_SwitchView(idx)"  v-for='(itemAddHead,idx) in address_listData'  :id="idx"  :key='idx' >地址{{idx+1}}</span>
						</div>
						<div class="addressContent">
							<div>
								<span>地址：</span>
								<span>{{currentAddressInfo.issueAddr1}}</span>
							</div>
							<div>
								<span>联系人：</span>
								<span>{{currentAddressInfo.issueUser}}</span>
							</div>
							<div>
								<span>电话：</span>
			 					<span>{{currentAddressInfo.issuePhone}}</span> &nbsp;&nbsp;&nbsp;&nbsp;  <i style="color:#5965D8; " @click="callPhone(currentAddressInfo.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
							</div>
						</div>
					</div>
				</div>
		</div>
		<!-- 导航地址弹出框 -->
		<div class="Navigate_MultiAddress" v-if="typeMShow">
			<div>
				<div class="SelectTitle">请选择导航地址</div>
				<div class="SelectContent">
					<div class="addressContent" @click="Navigate_SelectComit(itemAddress.issueAddr1)"  v-for='(itemAddress,idx) in Navigate_addressList'  :id="idx"  :key='idx'>
						<div>
							<span>{{itemAddress.issueAddr1}}</span>
						</div>
						<div>
							<span>{{itemAddress.issueUser}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span>{{itemAddress.issuePhone}}</span><i style="color:#2B5FD8;" @click="callPhone(itemAddress.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
						</div>
					</div>
				</div>
				<div class="cancel"   @click="selectCancel('e')">取消</div>
			</div>
		</div>
		<!-- 页面数据为空时 -->
		<div   v-if="is_NoData"   class="NoData" >
			<div>
				<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
				<div>{{is_NoData_text}}</div>
			</div>
		</div>
		<FooterIndex/>
	</div>
</template>
<script type="text/javascript">
import FooterIndex from '../../components/footer.vue'
import $ from "jquery"
	export default{
		name:"Waybill",
		data(){
			return{
				msg:"",//提示信息
				bigListData:[],
		        addressWaybill_Idx:0,//配载单idx（点击了哪一个多装货地址配载单）
		        currentAddressInfo:[],// 当前显示的地址数组信息
		        address_listData:[],// 当前多地址配载单的地址数组信息 bigListData[i].issueAddrLiat
		        Exceed:false,//预约时间是否超出当前时间
		        is_NoData:false,//没有数据模块样式  是否显示
		        is_NoData_text:"没有任务",//没有数据模块样式  显示的文字
		        

		        typeMShow:false,//导航地址选择弹出框 是否显示
		        Navigate_addressList:[],//导航地址列表
		        NoSearchFrom:true,//判断是否为搜索页面转来 false为搜索页面转来

		        currentPage: 1,//当前第几页
				numberPerPage: 5,//每页显示几条
				loadPage: 1 ,//当前第几页
				isFinished: 0,//上拉刷新是否完成
			}
		},
		components:{
		  	FooterIndex
		},
		mounted(){

			var that = this;

		},
		created(){

			var that = this;

			this.NoSearchFrom = that.$store.state.NoSearchFrom;

			this.loadData();
		},
		methods:{
			// 加载页面数据
			loadData(){

				var that = this;

				var WaybillData = that.$store.state.WaybillData;

				// 需要刷新
				if(that.$store.state.Waybill_needRefresh || !WaybillData.length){

					var postData = {
					    currentPage: that.currentPage,//当前第几页
					    numberPerPage: that.numberPerPage,//每页显示几条
					    startDate: null,//开始时间
					    endDate: null,//结束时间
					    shipmentCodeinput: "",//配载单号  P1001808150006  P1001809100002
					    deliveryNoinput: null,//订单号
					    cellphone: that.$store.state.userInfo.cellphone,//手机号   15013418360
				    };
					
					this.httpRequest("queryShipmentData.do",postData,function(res){

						// that.$refs.loadmore.onTopLoaded();

						//上拉加载后 数据拼接
				        if (that.loadPage > 1) {
					   
					        that.bigListData = that.bigListData.concat(res.data)
					      
				        } else {
					       
					        that.bigListData = res.data;

							if(res.data.length){

								that.is_NoData = false; //没有数据模块 是否显示

							}else{
								that.is_NoData_text = "没有任务"

								that.is_NoData = true;
							}
				        }

				        that.$store.state.WaybillData = that.bigListData;

					    // 判断是否能上拉加载
					    if (res.data.length < that.numberPerPage) {
					 
					        that.isFinished = 1;
				
					    } else {
					        
					        that.isFinished = 0;
					      
					    }
					})
				}else{

					that.bigListData = WaybillData;

					// that.$refs.loadmore.onTopLoaded();

					if(that.bigListData.length){

						that.is_NoData = false; //没有数据模块 是否显示

					}else{

						that.is_NoData = true;

						that.is_NoData_text = "没有结果"
					}

					this.$emit('isLoading', false);
				}
			},
			// 多装货地址弹框出现
			MultiAddress(index){

				// 配载单idx（点击了哪一个多装货地址配载单）
				this.addressWaybill_Idx = index;

				this.address_listData = this.bigListData[index].issueAddrLiat;
				// 当前显示的地址数组信息
				this.currentAddressInfo = this.bigListData[this.addressWaybill_Idx].issueAddrLiat[0];

				$(".MultiAddress").css("display","block");

				$('.addressHead>span').eq(0).css("border-bottom-color","#5965D8").siblings().css("border-bottom-color","#eee");
			},
			// 关闭地址弹框
			closeAddress(){

				$(".MultiAddress").css("display","none");
			},
			// 在多地址弹框中 切换查看地址
			address_SwitchView(idx){

				// 当前显示的地址数组信息
				this.currentAddressInfo = this.bigListData[this.addressWaybill_Idx].issueAddrLiat[idx];
				$('.addressHead>span').eq(idx).css("border-bottom-color","#5965D8").siblings().css("border-bottom-color","#eee");
			},
			// 点击导航按钮
			Navigation(idx){

				var that = this;

				this.Navigate_addressList = [];

				// 装货地址列表
				var loading_addressList = that.bigListData[idx].issueAddrLiat;

				// 送货地址列表
				var Delivery_List = that.bigListData[idx].deliveryList

				var Delivery_addressList = [];
				for(var m = 0; m < Delivery_List.length; m++){

					Delivery_addressList[m] = {
						issueAddr1: Delivery_List[m].recAddr1,//地址
						issuePhone: Delivery_List[m].recPhone,//电话
						issueUser: Delivery_List[m].recName,//联系人
					}
				}

				this.Navigate_addressList = loading_addressList.concat(Delivery_addressList);

				this.typeMShow = true;
			},
			// 选择具体要 导航的地址
			Navigate_SelectComit(Navigate_detailAddress){

				// 选择的导航具体地址
				var  Navigate_detailAddress= Navigate_detailAddress;

				console.log(Navigate_detailAddress);

				this.typeMShow = false;

				// 安卓
				try {
					CallAndroidOrIOS.callAndroid("导航", Navigate_detailAddress);
				} 
				catch(error) {
					console.log("没有CallAndroidOrIOS.callAndroid方法")
				}
				// 苹果
				try {
					CallAndroidOrIOS("导航", Navigate_detailAddress);
				}
				catch(error) {
					console.log("没有CallAndroidOrIOS方法")
				}
			},
			// 跳转到 装货预约 页面
			LoadingAppoint:function(idx){
				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"LoadingAppoint",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 到达确认 页面
			toArrivalConfirm:function(idx){

				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"ArrivalConfirm",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 途中反馈 页面
			toWayFeedback:function(idx){

				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"WayFeedback",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 发车确认 页面
			toDepartureConfirm:function(idx){

				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"DepartureConfirm",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 批量交付页面（按照送货地址）
			toOrderDetails:function(shipmentID,tenantCode,detailInfo){

				this.$store.state.WaybillInfo = {
					"detailInfo":detailInfo,//按地址交付页面 客户信息
					"shipmentID":shipmentID,//配置单id
					"tenantCode":tenantCode,//租户代码
				}
				
				this.$router.push({
					name:"OrderDetails",
					query:{
					}
				})
			},
			// 跳转到 卸货预约 页面
			toUnloadAppoint(vehicleCode,shipmentID,orgnizationId,recAddr1,tenantCode){

				event.stopPropagation();

				//vehicleCode, 车牌号
				//shipmentID,  配载单id
				// orgnizationId,  客户id
				//recAddr1     卸货地点
				// tenantCode    租户代码

				this.$router.push({
					name:"UnloadAppoint",
					query:{
						vehicleCode:vehicleCode,
						shipmentID:shipmentID,
						orgnizationId:orgnizationId,
						recAddr1:recAddr1,
						tenantCode: tenantCode
					}
				})
			},
			// 搜索后的列表  点击详情跳转到运单详情
			toOrderDeliver(deliveryList){

				this.$store.state.search_WaybillDetail = deliveryList

				this.$router.push({
					name:"WaybillDetail",
					query:{
					}
				})
			},
			// 下拉刷新
			loadTop() {

				console.log(8888)

		        this.loadPage = 1;

		        this.isFinished = 0;

				// 需要重新请求数据
				this.$store.state.Waybill_needRefresh = true;

				this.currentPage = 1;//当前第几页

				this.loadData();
			},
			// 上拉加载
			loadBottom(){

			    var that = this;

			    console.log(233)

			    if (that.isFinished){
			     
			        return;
			    }else{
			     
			        that.loadPage += 1;
			    }

				// 需要重新请求数据
				this.$store.state.Waybill_needRefresh = true;

				this.currentPage = that.loadPage ;//当前第几页

				this.loadData();

			}
		}
	}
</script>
<style lang="less" scoped>
	.Waybill{
		overflow: hidden;
	}
	.container{
		font-size: 29/50rem;
		margin-bottom: 105/50rem;overflow: hidden;
		.bigList{
			margin-bottom: 10/50rem;
			text-align: left;
			//.listHead 与 .smallLiat
			&>div{
				overflow: hidden;
				position: relative;
				width: 100%;
				padding: 20/50rem;
				box-sizing: border-box;	
				&>div{
					height: 55/50rem;
					line-height: 55/50rem;
					overflow: hidden;
				}
				.iconfont{
					font-size: 35/50rem;
					color: #2B5FD8;
					margin-right: 5/50rem;
				}
			}
			&>div.searchResult{
				padding:10/50rem 20/50rem;
				background-color: #fff;
				border-bottom: 8/50rem solid #EDEFF3;
				.place{
					overflow: hidden;
					height: 70/50rem;
			  		line-height: 70/50rem;
					border-bottom: 1/50rem solid #EDEFF3;
					span{
						float: right;
					}
					.dateLoading{
						float: left;
					}
				}
				.firstLine{
					margin-top: 20/50rem;
					i{
						margin:0 20/50rem;
					}
				}
				.detailBtn{
					height: 60/50rem;
					span{
						float: right;
						height:50/50rem;
						line-height: 50/50rem;
						width: 180/50rem;
						border-radius:10/50rem;
						color:#fff;
						background-color: #6897FF;
						text-align: center;
					}
				}
			}
			&>div.listHead{
				background-color: #F4F4F4;
				border-top-right-radius: 20/50rem;
				border-top-left-radius: 20/50rem;
				&>p{
					margin-top: 10/50rem;
					display: flex;
					span{
						flex-grow:1;
						margin-left: 15/50rem;
						text-align: center;
						background-color: #5965D8;
						color: #fff;
						border-radius: 15/50rem;
						padding: 8/50rem 0;
					}
					span:nth-child(1){
						margin-left:0;
					}
				}
				&>div{
					&.firstLine{
						width: 600/50rem;
						span{
							float: left;
							&:nth-child(1){
								margin-left: 180/50rem;
							}
						}
						.icon-jiang{
							margin:0 15/50rem;
							float: left;
						}
					}
				}
				.LicensePlate{
					position: absolute;
					top: 20/50rem;
					left: 0;
					background-color: #5965D8;
					padding:0  25/50rem;
					height: 50/50rem;
					line-height: 50/50rem;
					color: #fff;
					border-radius: 0 25/50rem 25/50rem 0;
				}
				.Navigation{
					position: absolute;
					right: 20/50rem;
					top: 20/50rem;
					width:100/50rem;
					height: 100/50rem;
					line-height: 100/50rem;
					background-color: #5965D8;
					border-radius: 50%;
					text-align: center;
					color: #fff;
				}
			}
			&:first-child{
				&>div.listHead{
					border-top-right-radius: 0;
					border-top-left-radius: 0;
				}
			}
			&>div.smallLiat{
				&:last-child{
					border-bottom: none;
				}
				border-bottom: 12/50rem solid #F3F4F4;
				&>p{
					height: 52/50rem;
					line-height: 52/50rem;
					padding-top: 18/50rem;
					border-top: 1/50rem solid #F3F4F4;
					span{
						&:nth-child(2){
							float: left;
							margin-left: 10/50rem;
						}
						&:nth-child(4){
							margin-right: 10/50rem;
						}
						&:nth-child(3),&:nth-child(4){
							border:1/50rem solid #6897FF;
							float: right;
							width:160/50rem;
							height: 50/50rem;
							line-height: 50/50rem;
							text-align: center;
							color: #6897FF;
							border-radius: 20/50rem;
						}
					}
				}
				span.status{
				    position: absolute;
				    width:120/50rem;
				    text-align: center;
				    right: 5/50rem;
				    top:20/50rem;
				    z-index: 99;
				    color: #fff;
				    transform:rotate(29.5deg);
				}
				&::before,&::after{
				 	content: "";
					width: 0;
					height: 0;
					display: block;
					position: absolute;
				    right: 0/50rem;
				    top: 0/50rem;
				    border-left:70/50rem solid transparent;
				    border-right: 0/50rem solid transparent;
					border-top: 35/50rem solid #fff;
					z-index: 33;
				}
				&::after{
				    color: #fff;
				    border-left:200/50rem solid transparent;
					border-top: 100/50rem solid #6897FF;
					z-index: 22;
				}
			}
		}
		.MultiAddress{
			width: 100%;
			height: 100%;
			position: fixed;
			top: 0;
			left:0;
			background-color: rgba(0,0,0,0.5);
			z-index: 99999;
			display: none;
			&>div{
				width: 600/50rem;
				height: 400/50rem;
				background-color: #fff;
				position: absolute;
				top: 50%;
				left: 50%;
				margin-top: -200/50rem;
				margin-left: -300/50rem;
				border-radius: 10/50rem;
				&>.icon-iconfontcuowu{
					position: absolute;
					top: -10/50rem;
					right: -10/50rem;
					font-size: 45/50rem;
				}
				&>.addressHead{
					width: 100%;
					height: 80/50rem;
					line-height: 80/50rem;
					display: flex;
					&>span{
						flex-grow:1;
						text-align: center;
						border-right: 1/50rem solid #eee;
						border-bottom: 1/50rem solid #eee;
					}
					&>span:first-child{
						border-bottom: 1/50rem solid #5965D8;
					}
					&>span:last-child{
						border-right:none;
					}
				}
				&>.addressContent{
					padding: 0 20/50rem;
					box-sizing: border-box;
					&>div{
						margin-top: 30/50rem;
					}
				}
			}
		}
	}
	//没有数据 样式
	.NoData{
	    width: 100%;
	    height: 100%;
	    position: fixed;
	    top: 0;
	    left: 0;
	    margin-top: -200/50rem;
	    font-size: 40/50rem;
	    &>div{
		    width: 100%;
		    height: 180/50rem;
		    position: absolute;
		    top: 50%;
		    left: 0;
		    text-align: center;
		    color: #bbb;
		    &>.icon-meiyouwuliuxinxi{
			    font-size: 150/50rem;
			}
		}
	}
	// 374像素以下（包含374像素）
	@media screen and (max-width: 374px){
		.container{
			.bigList{
				&>div.smallLiat{
					&>p{
						border-top: 3/50rem solid #F3F4F4;
						span{
							&:nth-child(3),&:nth-child(4){
								border:3/50rem solid #6897FF;
							}
						}
					}
				}
			}
			.MultiAddress{
				&>div{
					&>.addressHead{
						&>span{
							border-right: 3/50rem solid #eee;
							border-bottom: 3/50rem solid #eee;
						}
						&>span:first-child{
							border-bottom: 3/50rem solid #5965D8;
						}
					}
				}
			}
		}	
	}
</style>


