<template>
	<div class="WaybillDetail">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span>运单详情</span></header>
		<div class="container">
			<div class="smallLiat"  v-for='(itemData,idx) in deliveryList'  :id="idx"  :key='idx' >
				<span class='status'>{{itemData.status}}</span>
				<div>
					<span>客户名称：</span>
					<span>{{itemData.name}}</span>
				</div>
				<div>
					<span>送货地址：</span>
					<span>{{itemData.recAddr1}}</span>
				</div>
				<div>
					<span>联&nbsp; 系&nbsp;人：</span>
					<span>{{itemData.recName}}&nbsp;&nbsp;&nbsp; {{itemData.recPhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i @click="callPhone(itemData.recPhone)" class="iconfont icon-dianhua-copy" ></i>
				</div>
				<div>
					<span>运&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;量：</span>
					<span>{{(itemData.shippedVol * 0.000001).toFixed(3)}} 立方米 &nbsp;&nbsp; </span>
					<span>{{(itemData.shippedWt * 0.000001).toFixed(3)}} 吨 &nbsp;&nbsp; </span>
					<span>{{itemData.shippedQty}} 箱</span>
				</div>
				<div>
					<span>预约时间：</span>
					<span>{{itemData.phoneAppointTime}}</span>
				</div>
			</div>
		</div>
		<div v-if="ifTips" class="msg_tips">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
	</div>
</template>
<script type="text/javascript">
	
	export default{
		name:"WaybillDetail",
		data(){
			return{
				deliveryList:[],
				tips_Msg:"",//提示信息
				ifTips:false,//提示信息是否显示

			}
		},
		created(){

			this.deliveryList = this.$store.state.search_WaybillDetail;

		},
		methods:{
		}
	}
</script>
<style lang="less" scoped>
	.WaybillDetail{
		overflow: hidden;
		.container{
			&>div{
				overflow: hidden;
				position: relative;
				width: 100%;
				padding: 20/50rem;
				box-sizing: border-box;	
				&>div{
					height: 55/50rem;
					line-height: 55/50rem;
					overflow: hidden;
				}
				.iconfont{
					font-size: 35/50rem;
					color: #2B5FD8;
					margin-right: 5/50rem;
				}
			}
			&>div.smallLiat{
				border-bottom: 12/50rem solid #F3F4F4;
				span.status{
				    position: absolute;
				    width:120/50rem;
				    text-align: center;
				    right: 5/50rem;
				    top:20/50rem;
				    z-index: 99;
				    color: #fff;
				    transform:rotate(29.5deg);
				}
				&::before,&::after{
				 	content: "";
					width: 0;
					height: 0;
					display: block;
					position: absolute;
				    right: 0/50rem;
				    top: 0/50rem;
				    border-left:70/50rem solid transparent;
				    border-right: 0/50rem solid transparent;
					border-top: 35/50rem solid #fff;
					z-index: 33;
				}
				&::after{
				    color: #fff;
				    border-left:200/50rem solid transparent;
					border-top: 100/50rem solid #6897FF;
					z-index: 22;
				}
			}
		}
	}
</style>


