import Vue from "vue"
import vuex from "vuex"

Vue.use(vuex);

export default new vuex.Store({
	state:{
		userInfo:[],//登录页面 登录成功后获得的用户信息列表

		typeOfCarList:[],//注册页面 车型列表

		WaybillData:[],//我的任务  页面数据
		NoSearchFrom:true,//我的任务 判断是否从搜索页面转来
		Waybill_needRefresh:true,//我的任务 页面数据是否需要刷新
		WaybillInfo:{},//按地址交付页面数据   从我的任务获得   运单详情（同一个客户地址，多个订单）(点击交付)
		deliveryList:[],//按地址交付 订单列表
		Device:"",//用户使用的设备（例如iOS, android）


		
	}
})


