//http请求
Vue.prototype.httpRequest=function (url,params,success){

	var that = this;

	that.$emit('isLoading', true,"拼命加载中");

	Axios.post(that.HOST+url,{"params": JSON.stringify(params)})
	.then(function(res){
		console.log(res)

		that.$emit('isLoading', false);

		if(res.data.status==1){

			success(res.data);

		}else{

			var msg;
			if(res.data.Msg){

				msg = res.data.Msg;

			}else{

				msg = "请求失败";
			}

			that.$alert(msg, '提示', {
	            confirmButtonText: '确定',
	            callback: action => {
		            
	            }
           	})
		}
	})
	.catch(function(res){

		that.$emit('isLoading', false);
			
		that.$alert("请求失败", '提示', {
	        confirmButtonText: '确定',
	        callback: action => {
	            
	        }
	   	})
		
	})
}

