<template>
  <div>
    <footer>
  <!--     <router-link  :footTagIdx="checkindex" v-for='(dataItem,index) in footList'  :id="index"  :key='index'  :class="{'active':index == checkindex }" @click="toggle(index,dataItem.path);scrollToFoods(index)" tag="div"  > -->
        <!-- :to="{path:dataItem.path}" -->
     <!--    <i  class="iconfont" :class="dataItem.icon" ></i>
        <p>{{dataItem.title}}</p>
      </router-link> -->
      <router-link tag="div" :to="{path:'/Waybill'}" >
      <i class="iconfont icon-woderenwu" ></i>
      <p class='p'>我的任务</p>
    </router-link>
    <router-link tag="div" :to="{path:'/FindGoods'}"  >
      <i class="iconfont icon-zhaohuo"></i>
      <p class='p'>我要找货</p>
    </router-link>
    <router-link tag="div" to="/GrabGoods" >
      <i class="iconfont icon-qiang"></i>
      <p class='p'>抢货竞价</p>
    </router-link>
    <router-link tag="div" to="/HomeIndex" >
      <i class="iconfont icon-gerenzhongxin"></i>
      <p class='p'>个人中心</p>
    </router-link>
    </footer>
  </div>

</template>

<script>

export default {
    name:"FooterIndex",
    data(){
      return{
        footList:[
          {
            path: "/Waybill",
            title: "我的任务",
            icon: "icon-woderenwu"
          },
          {
            path: "/FindGoods",
            title: "我要找货",
            icon: "icon-zhaohuo"
          },
          {
            path: "/GrabGoods",
            title: "抢货竞价",
            icon: "icon-qiang"
          },
          {
            path: "/HomeIndex",
            title: "个人中心",
            icon: "icon-gerenzhongxin"
          },
        ],
        checkindex:0,
      }
    },
    methods:{
      toggle(index,path){

        
        // // 需要重新请求数据
        // this.$store.state.Waybill_needRefresh = false;


        console.log(this.checkindex)

        this.$router.push(path)
        this.checkindex = index;

      }
    }
}
</script>

<style lang="less" scoped>

//页脚样式 开始


footer{
  position:fixed;
  z-index:999;
  background:#F8F8F8;
  bottom:0;
  left:0;
  height:111/50rem;
  width:100%;
  display:flex;
  div{
    flex-grow:1;
    float:left;
    text-align:center;
    i{
      font-size: 45/50rem;
    }
  }
  div:nth-child(1){
    color: #5965D8;
    p{
      color: #5965D8;
    }
  }
}
.p{
  color:#A0A0A0;
  font-size:25/50rem;
  margin-bottom:10/50rem;
}
.active>i,.active>.p{
  color:#A54BE8;
}
//页脚样式 结束
</style>



