<template>
	<div class="HomeIndex">
		<header><span>个人中心</span></header>
		<div class="container">
			<div class='home_container'>
			  	<div class='headImg'>
				    <div class='background'></div>
				    <div class='background_blank'></div>

				    <div class='head'>
				    	<div class="image"></div>
				    	<img  class="userinfo-avatar" src="../../assets/images/defaultHead.png">
				        <!-- <image  class="userinfo-avatar" src="../../assets/images/defaultHead.png" ></image> -->
				         <!-- src="{{hasUserInfo ? userInfo.avatarUrl : '../../lib/images/defaultHead.png'}}" -->
				        <div class='userName'>{{userName}}</div>
				        <div class='userName'>{{cellphone}}</div>
				    </div>
			    
			    	<div class='itemInfo'>
					    <div class='line'></div>
					    <div class='detailInfo'></div>
					    <div class='line'></div>
					    <div class='detailInfo'></div>
					    <div class='line'></div>
					    <div class='detailInfo'></div>
					    <div class='line'></div>
			    	</div>
				    <div class='Exit_logon'  @click="exit">
					    <i class="iconfont icon-icon-yxj-switch-account" ></i>
					    <div>切换账号</div>
				    </div>
			 	</div>
			</div>
		</div>
		<FooterIndex/>
	</div>
</template>
<script type="text/javascript">
import FooterIndex from "../../components/footer"
	export default{
		name:"HomeIndex",
		data(){
			return{
				userName:"",
				cellphone:"",//手机号码
			}
		},
		components:{
			FooterIndex
		},
		created(){

			this.userName = this.$store.state.userInfo.userName;
			this.cellphone = this.$store.state.userInfo.cellphone;
		},
		methods:{
			exit(){
				this.$router.push({
					name:"Login",
					query:{
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.HomeIndex{
		overflow: hidden;
		.container{
			.home_container{
			  width: 100%;
			  height: 100%;
			  font-size: 26/50rem;
				.headImg{
				    width: 100%;
				    height: 100%;
				    position: relative;
				    background-color: #F4F4F4;
				    overflow: hidden;
				  	.userName{
					  font-size: 38/50rem;
					  color: #6897FF;
					  text-align: center;
					  margin-bottom: 40/50rem;
					}
					.head{
						//.image{
						//	width: 200/50rem;
						//	height: 200/50rem;
						//	display: block;
						//}
					}
				}
			}
			.background{
			  width:100%;
			  height: 350/50rem;
			  background: linear-gradient(to bottom,#5965D8,#C4CAEF);
			}
			.background_blank{
			  width: 650/50rem;
			  height: 350/50rem;
			  position: absolute;
			  top:150/50rem;
			  left: 50/50rem;
			  background-color: #fff;
			  z-index: 33;
			  border-radius: 10/50rem;
			}
			.head{
			  width:100%;
			  height: 350/50rem;
			  position: absolute;
			  left: 0/50rem;
			  z-index: 999;
			  top: 75/50rem;
			}
			.userinfo-avatar{
			  width: 150/50rem;
			  height: 150/50rem;
			  border-radius: 50%;
			  display: block;
			  margin: 0 auto 40/50rem; 
			}
			.itemInfo{
			  width: 100%;
			  height: 250/50rem;
			  margin-top: 250/50rem;
			  position: relative;
				.line{
				  width: 97/50rem;
				  background-color: #6897FF;
				  height: 6/50rem;
				}
				.detailInfo{
				  width: 114/50rem;
				  height: 114/50rem;
				  border: 6/50rem solid #6897FF;
				  border-radius: 50%;
				}
				&>div{
				  position: absolute;
				  left: 0;
				  top: 54/50rem;
				}
				&>div:nth-child(2){
				  left: 93/50rem;
				  top: 0;
				}
				&>div:nth-child(2)::after,&>div:nth-child(4)::after,&>div:nth-child(6)::after{
				  font-family: 'iconfont';
				  font-weight: 600;
				  font-style: normal;
				  position: absolute;
				  color: #6897FF;
				  content:"\e668";
				  font-size:70/50rem;
				  top:22/50rem;
				  left:22/50rem;
				}
				&>div:nth-child(2)::before,&>div:nth-child(4)::before,&>div:nth-child(6)::before{
				  content:"上报位置";
				  position: absolute;
				  font-size:26/50rem;
				  width: 150/50rem;
				  text-align: center;
				  top:140/50rem;
				  left:-18/50rem;
				  color: #6897FF;
				}
				&>div:nth-child(3){
				  left: 215/50rem;
				  width: 102/50rem;
				}
				&>div:nth-child(4){
				  	left: 316/50rem;
				  	top: 0;
				  	&::after{
					  content:"\e698";
					  font-size:75/50rem;
					  top:19.5/50rem;
					  left:19.5/50rem;
					}
					&::before{
					  content:"费用统计";
					}
				}
				&>div:nth-child(5){
				  left: 434/50rem;
				  width: 101/50rem;
				}
				&>div:nth-child(6){
				  left:531/50rem;
				  top: 0;
				  	&::before{
					  content:"个人资料";
					}
					&::after{
					  font-size: 65/50rem;
					  content:"\e648";
					  top:24.5/50rem;
					  left:24.5/50rem;
					}
				}
				&>div:nth-child(7){
				  left: 653/50rem;
				  width: 99/50rem;
				}
			}
			///* 切换账号 */
			.Exit_logon{
			  	width: 100%;
			 	height: 300/50rem;
			  	&>i{
				 	display: block;
				 	margin: 0 auto;
				 	width: 114/50rem;
				 	font-size: 114/50rem;
				 	color: #6897FF;
				}
				&>div{
				  text-align: center;
				  color: #6897FF;
				  font-size: 28/50rem;
				  margin-top: 10/50rem;
				}
			}
		}
	}
</style>


