<template>
	<div class="OrderDetails">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span>订单交付</span></header>
		<div class="container">
			<div class='WaybillInfo'>
			    <div>客户名称：<span>{{CustomerInfo.name}}</span></div>
			    <div>送货地址：<span>{{CustomerInfo.recAddr1}}</span></div>
			    <div>
					联 &nbsp;系&nbsp;人：
					<span>{{CustomerInfo.recName}}&nbsp;&nbsp;&nbsp; {{CustomerInfo.recPhone}}</span>&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;<i @click="callPhone(CustomerInfo.recPhone)" class="iconfont icon-dianhua-copy" style="color: #2B5FD8" ></i>
				</div>
		
		    	<div>运&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;量：<span>{{CustomerInfo.shippedVol}}方 &nbsp;&nbsp;&nbsp; </span><span>{{CustomerInfo.shippedWt}}吨 &nbsp;&nbsp;&nbsp; </span><span>{{CustomerInfo.shippedQty}}箱</span></div>
			    <div>预约时间：<span>{{CustomerInfo.phoneAppointTime}}</span></div>
		
			</div>
			<div class="orderHead" >订单信息</div>
			<div class='goodsInfo'   v-for='(dataItem,index) in deliveryList'  :id="index"  :key='index'  @click="selectData(index,dataItem.ifDeliver)">
			    <div class='goodsHead'><span v-if="dataItem.ifDeliver" class="iconfont icon-xuanzhong"></span><span class="commodityInfo">货物信息</span><span class="OrderNum">订单号：{{dataItem.omsNo}}</span></div>
			    <div class='goodsContent'>
				    <div   v-for='(Item,idx) in dataItem.deliveryDetailList'  :id="idx"  :key='idx'>{{Item.productNo}} &nbsp;&nbsp;&nbsp;  {{Item.productName}}&nbsp;&nbsp;&nbsp;      {{Item.poOrderQty}}{{Item.poUom}}</div>
			    </div>
			    <div class="goodsBtn">
			    	<span v-if="dataItem.ifDeliver" class="g_btn" @click="toOrderDeliver(index)">交付</span>
			    	<span v-else class="g_btn" style="background-color: #d5d5d5;">交付</span>
			    </div>
			</div>
			<div class="g_doubleBtn bottomBtn">
                <div   class="selectAll"  @click="selectAll">取消全选</div>
                <div @click="toOrderDeliver('selectAll')">批量交付</div>
            </div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import $ from "jquery"
	export default{
		name:"OrderDetails",
		data(){
			return{
				WaybillInfo:[],//上一页面传来的数据（vuex里）
				CustomerInfo:[],//头部 客户信息
				deliveryList:[],//订单信息 数据
			}
		},
		created(){

			var that = this;

			this.WaybillInfo = this.$store.state.WaybillInfo;//上一页面传来的数据（vuex里）

			this.CustomerInfo = this.$store.state.WaybillInfo.detailInfo;//头部 客户信息

			var WaybillInfo = this.$store.state.WaybillInfo;

			var postData = {
				shipId: WaybillInfo.shipmentID,//配置单ID
				orgnizationId: WaybillInfo.detailInfo.orgnizationId,//客户ID
				receiveAddr: WaybillInfo.detailInfo.recAddr1,//送货地址
				tenantCode: WaybillInfo.tenantCode,//租户代码
			}
			
			this.httpRequest( "queryDeliverDetailInfo.do",postData,function(res){

				if(res.data.length){

					that.deliveryList = res.data;

					that.$store.state.deliveryList =  res.data;

					// 判断订单是否可以进行交付动作
					for(var m = 0; m < res.data.length; m++){

						if( res.data[m].status == "在途" || res.data[m].status == "部分交付" ){
							// 可交付
							that.deliveryList[m].ifDeliver = true;

						}else{
							// 不可交付
							that.deliveryList[m].ifDeliver = false;
						}
					}
				}else{

					// that.tipsShow();

					// that.msg = "未查询到数据";
					//不需要弹出框提示，页面显示“没有数据”

				}
			})
		},
		methods:{
			// 跳转到 订单交付页面
			toOrderDeliver(index){

				event.stopPropagation();

				var that = this;

				var deliverNo_list = [];

				// 交付
				if(index != "selectAll"){

					deliverNo_list[0] = that.deliveryList[index].deliveryId;

					that.$router.push({
						name:"orderDeliver",
						query:{
							deliverNo_list:deliverNo_list
						}
					})

					return;

				// 批量交付
				}else{

					for(var i = 0 ; i < that.deliveryList.length;i++){

						if($(".goodsInfo").eq(i).children(".goodsHead").children("span.iconfont").hasClass("icon-xuanzhong")){

							deliverNo_list[deliverNo_list.length] = that.deliveryList[i].deliveryId;
						}
					}

					if(!deliverNo_list.length){

						that.$alert("没有选中任何订单", '提示', {
					        confirmButtonText: '确定'
					   	})

					}else{
						that.$router.push({
							name:"orderDeliver",
							query:{
								deliverNo_list:deliverNo_list
							}
						})
					}
				}

				console.log(deliverNo_list)
			},
			//点击勾选订单
			selectData:function(idx,ifDeliver){

				var that = this;

				if(!ifDeliver){
					return;
				}

				var iconElement = $(".goodsInfo").eq(idx).children(".goodsHead").children("span.iconfont");
				
				if(iconElement.hasClass("icon-xuanzhong")){

					iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong")

				}else if(iconElement.hasClass("icon-weixuanzhong")){

					iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
				}

				var if_Element;
				var if_changeBtn = {
					Selected:false,
					unSelected:false
				};
				for(var n = 0; n < that.deliveryList.length; n++){

					if_Element = $(".goodsInfo").eq(n).children(".goodsHead").children("span.iconfont");

					if(if_Element.hasClass("icon-xuanzhong")){

						if_changeBtn.Selected = true;

					}else if(if_Element.hasClass("icon-weixuanzhong")){

						if_changeBtn.unSelected = true;

					}
				}

				if(if_changeBtn.Selected != if_changeBtn.unSelected){

					if($(".selectAll").html() == "取消全选"){

						$(".selectAll").html("全选");

					}else{
						$(".selectAll").html("取消全选");
					}
				}
			},
			// 点击全选按钮
			selectAll(){

				var that = this;

				if($(".selectAll").html() == "取消全选"){

					$(".selectAll").html("全选");

					var iconElement;

					for(var j = 0; j < that.deliveryList.length; j++){

						iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

						iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong");
					}

				}else{

					$(".selectAll").html("取消全选");

					var iconElement;

					for(var j = 0; j < that.deliveryList.length; j++){

						iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

						iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
					}
				}
			}
		}
	}
</script>
<style lang="less" scoped>
	.OrderDetails{
		height: 100%;
		width: 100%;
		.container{
			padding-bottom: 150/50rem;
			width:100%;
		  	font-size: 29/50rem;
		  	overflow: hidden;
			.WaybillInfo{
				padding:0 16/50rem;
				box-sizing: border-box;
				background-color: #fff;
			  	overflow: hidden;
			  	&>div{
				  	overflow: hidden;
				  	height: 60/50rem;
				  	line-height: 60/50rem;
					&>div{
					  	float: left;
					  	width: 375/50rem;
					}
				}
			}
			.orderHead{
				text-align: center;
				height: 65/50rem;
				line-height: 65/50rem;
				color: #000;
				background-color: #EDEFF3;
			}
			.goodsInfo{
				color: #777;
				padding:0 16/50rem;
				box-sizing: border-box;
			  	border-bottom: 8/50rem solid #EDEFF3;
			  	background-color: #fff;
			  	.goodsHead{
			  		overflow: hidden;
			  		height: 70/50rem;
			  		line-height: 70/50rem;
			  		border-bottom: 1/50rem solid #EDEFF3;
			  		span.iconfont{
						color:#285DCE;
						font-size: 40/50rem;
						float: left;
						margin-right: 20/50rem;
			  		}
			  		span.commodityInfo{
			  			float: left;
			  		}
			  		span.OrderNum{
			  			float: right;
			  			color: #999;
			  		}
			  	}
			  	.goodsContent{
			  		&>div{
			  			margin-top: 20/50rem;
			  		}
			  	}
			  	.goodsBtn{
			  		height: 80/50rem;
			  		margin-top: 20/50rem;
					span{
						float: right;
						height:60/50rem;
						line-height: 60/50rem;
						width: 200/50rem;
						
					}
			  	}
			}
			//按钮 此页面公共样式
			span.g_btn{
				border-radius:10/50rem;
				color:#fff;
				background-color: #6897FF;
				text-align: center;
			}
			.bottomBtn{
				width: 100%;
				height: 90/50rem;
				position: fixed;
				bottom: 0;
				div{
					border-radius: 35/50rem;
				}
			}
		}
	}
</style>


